export default {
	props: {
		items: {
			type: Array,
			required: true,
		},
		holder: {
			default: null,
			type: Array
		},
		index: {
			default: null,
			type: Array,
		}
	},
	data() {
		return {
			indexToggle: -1,
			current: [0],
			visible: false,
			action: false,
			animateTime: 200
		};
	},
	mounted() {
		this.current=Array(this.items.length).fill(0);
		if(this.holder)this.current = this.holder.map(each => (each ? -1 : 0));
		if (this.index) this.current = this.index;
		console.log(this.current, this.holder, this.items);
	},
	methods: {
		toggleChange(e) {
			let sw = this.indexToggle !== e.target.dataset.i;
			if (sw) {
				this.visible = sw;
				this.indexToggle = e.target.dataset.i
			} else {
				this.visible = sw;
				let t = setTimeout(() => {
					this.indexToggle = -1;
					clearTimeout(t);
				}, this.animateTime)
			}
		},
		itemChange(e) {
			let i = e.target.dataset.i;
			this.current[this.indexToggle] = i;
			this.toggleChange({
				target: {
					dataset: {
						i: this.indexToggle
					}
				}
			})
			 this.$emit('change',{
				value: this.items[this.indexToggle][i],
				index:i,
			})
		},
	}
}
